import { Injectable, EventEmitter } from '@angular/core';
import { HttpClient } from '@angular/common/http';
@Injectable({
  providedIn: 'root'
})
export class InfoService {
  infoQuote = {
    tripQuote: {
      Destination: "Canada",
      CountryOfOrigin: "USA",
      Province: "",
      VisitorType: "",
      EffectiveDate: "",
      ExpiryDate: "",
      TripDuration: "",
      NumberOfTravelers: "",
      CoverageType: "",
      Travelers: [{
        Year: "",
        Month: "",
        Day: "",
        Birthday: "",
        Age: "",
        Gender: "",
        FirstName: "",
        LastName: ""
      }],
      Address: {
        street: "",
        unitNum: "",
        city: "",
        province: "",
        workPhone: "",
        email: "",
        verifyEmail: "",
      }
    },
    selectCoverage: {
      Premium: 0,
      Amount: "",
      SelectDeductible: "$0",
      Discount: 0,
    },
    paymentMethod: {
      name: "",
      numCard: "",
      monthCard: "",
      yearCard: "",
      cvvCard: ""
    }
  }

  step1Complete;
  step2Complete;
  step3Complete;

  stepEvent = new EventEmitter();

  constructor(private http: HttpClient) { }

  getTripQuote(){
    return this.infoQuote.tripQuote;
  }

  setTripQuote(obj){
    this.infoQuote.tripQuote = obj;
  }

  getSelectCoverage(){
    return this.infoQuote.selectCoverage;
  }

  setSelectCoverage(obj){
    this.infoQuote.selectCoverage = obj;
  }

  getCovaregeData(){
    return this.http.get('../assets/json/response.json').toPromise();
  }

  getAddress(){
    this.infoQuote.tripQuote.Address
  }
}
