import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { InfoService } from 'src/app/shared/services/info.service';

@Component({
  selector: 'app-step4',
  templateUrl: './step4.component.html',
  styleUrls: ['./step4.component.css']
})
export class Step4Component implements OnInit {

  terms;

  constructor(private router: Router, private infoService: InfoService) {
   }

  ngOnInit() {
    this.infoService.stepEvent.emit('');
  }
  
  asValid() {
    return !this.infoService.infoQuote.paymentMethod.cvvCard || !this.infoService.infoQuote.paymentMethod.monthCard ||
    !this.infoService.infoQuote.paymentMethod.name || !this.infoService.infoQuote.paymentMethod.numCard || 
    !this.infoService.infoQuote.paymentMethod.yearCard || !this.terms;
  }

  next(){
  }
  
  back(){
    this.infoService.step3Complete = false;
    this.router.navigate(['/quote/step3']);
  }

}
