import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { QuoteComponent } from './quote/quote.component';
import { Step1Component } from './quote/step1/step1.component';
import { Step3Component } from './quote/step3/step3.component';
import { Step2Component } from './quote/step2/step2.component';
import { Step4Component } from './quote/step4/step4.component';

const routes: Routes = [
  { path: '', pathMatch: 'full', redirectTo: '/quote/step1'},
  {
    path: 'quote', component: QuoteComponent,
    children: [
      { path: 'step1', component: Step1Component },
      { path: 'step2', component: Step2Component },
      { path: 'step3', component: Step3Component },
      { path: 'step4', component: Step4Component },
    ]
  },

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
