import { Component, OnInit, ChangeDetectorRef, AfterViewChecked } from '@angular/core';
import { InfoService } from '../shared/services/info.service';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-quote',
  templateUrl: './quote.component.html',
  styleUrls: ['./quote.component.css']
})
export class QuoteComponent implements OnInit, AfterViewChecked {
  url = "";
  constructor(private info: InfoService,private router: Router,
    private activatedRoute: ActivatedRoute, private cdRef:ChangeDetectorRef) { 
      this.info.stepEvent.subscribe((data) => {
        this.url = this.router.url;
      })
    }

  ngOnInit() {

  }

  ngAfterViewChecked()
{
  this.cdRef.detectChanges();
}

}
