import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { InfoService } from 'src/app/shared/services/info.service';
import * as moment from 'moment';

@Component({
  selector: 'app-step3',
  templateUrl: './step3.component.html',
  styleUrls: ['./step3.component.css']
})
export class Step3Component implements OnInit {

  months = moment.months();
  years = [];
  daysInMonth = [];
  Address = {
    street: "",
    unitNum: "",
    city: "",
    province: "",
    workPhone: "",
    email: "",
    verifyEmail: "",
  };
  Travelers = [];
  showError = false;

  constructor(private router: Router, private infoService: InfoService) {
    if(infoService.infoQuote.tripQuote.Travelers.length != +infoService.infoQuote.tripQuote.NumberOfTravelers) {
      for (let index = 1; index < +infoService.infoQuote.tripQuote.NumberOfTravelers; index++) {
        this.Travelers.push({
          Year: "",
          Month: "",
          Day: "",
          Birthday: "",
          Age: "",
          Gender: "",
          FirstName: "",
          LastName: ""
        })
      }
      this.Travelers.unshift(infoService.infoQuote.tripQuote.Travelers[0]);
    } else {
      this.Travelers = infoService.infoQuote.tripQuote.Travelers;
    }

    this.Address = infoService.infoQuote.tripQuote.Address;

  }

  ngOnInit() {
    for(let i = 1900; i < moment().year(); i++){
      this.years.push(i);
    }
    for(let i = 1; i <= 31; i++){
      this.daysInMonth.push(i);
    }
    this.infoService.stepEvent.emit('');
   }

  onChangeDay(e, index) {
    this.Travelers[index].Day = e;
    this.getAge(index);
  }

  onChangeMonth(e, index) {
    this.Travelers[index].Month = e;
    this.getAge(index);
  }

  onChangeYear(e, index) {
    this.Travelers[index].Year = e;
    this.getAge(index);
  }

  getAge(index) {
    if (this.Travelers[index].Year && this.Travelers[index].Month && this.Travelers[index].Day) {
      this.Travelers[index].Age = moment().diff(`${this.Travelers[index].Year}/${this.Travelers[index].Month}/${this.Travelers[index].Day}`, 'years');
      
      this.Travelers.forEach(element => {
        let birthday = element.Year + "-" + element.Month + "-" + element.Day;
          birthday = moment(birthday).format('DD-MMMM-YYYY');
          element.Birthday = birthday;
      });
    }
  }

  asValid() {
    let checkAllInput;
    let filledData = 0;

    this.Travelers.forEach(element => {
      if (element.Year && element.Month && element.Day && element.Birthday && element.Age &&
        element.Gender && element.FirstName && element.LastName) {
        filledData++
      }
      if (filledData == this.Travelers.length) {
        checkAllInput = false;
      } else {
        checkAllInput = true;
      }
    });
    return !this.Address.city || !this.Address.email || !this.Address.province || !this.Address.street ||
      !this.Address.unitNum || !this.Address.verifyEmail || !this.Address.workPhone || checkAllInput;
  }

  next(){
    this.showError = true;
    if (this.asValid() == false) {
      this.infoService.step3Complete = true;
      this.infoService.infoQuote.tripQuote.Travelers = this.Travelers;
      this.router.navigate(['/quote/step4']);
    }
  }

  back(){
    this.infoService.step2Complete = false;
    this.infoService.step3Complete = false;
    this.router.navigate(['/quote/step2']);
  }

}
