import { Component, OnInit } from '@angular/core';
import { InfoService } from 'src/app/shared/services/info.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-step2',
  templateUrl: './step2.component.html',
  styleUrls: ['./step2.component.css']
})
export class Step2Component implements OnInit {
  ProductDetail;
  covarageData;
  constructor(private infoService: InfoService, private router: Router) {
    this.covarageData =  Object.assign({}, this.infoService.getSelectCoverage());

    this.infoService.getCovaregeData().then((data:any) =>
      new Promise(resolve => 
        setTimeout(() => {
          this.covarageData.Amount = data.Products.ProductDetail[0].OptionName;

          this.ProductDetail = data.Products.ProductDetail;
          this.covarageData.Premium = this.ProductDetail[0].Cost;
        }, 2000)
      )
    );
   }

  ngOnInit() {
    this.infoService.stepEvent.emit('');
  }

  onChangeAmount(e, i){
    this.covarageData.Premium = this.ProductDetail[i].Cost;
  }

  next(){
    this.infoService.step2Complete = true;
    this.infoService.setSelectCoverage(this.covarageData);
    this.router.navigate(['/quote/step3']);
  }

  back(){
    this.infoService.step1Complete = false;
    this.infoService.step2Complete = false;
    this.router.navigate(['/quote/step1']);
  }

}
